.mainmyorders{
    width: 100%;
    height: 100vh;
    overflow: hidden;
}
.myordermain{
    width: 100%;
    height: 100vh;
    background-color: rgb(236, 214, 221);
    padding: 20px;
    display: flex;
    justify-content: center;
}
.myorderbox1{
    width: 100%;
    height: 95vh;
    background-color: aliceblue;
    margin: 0px 10px;
    padding: 20px;
    overflow-y: scroll;
}
.myorderlist{
    width: 100%;
    height: 180px;
    background-color: white;
    display: flex;
    margin: 20px 0;
}
.myorderlist .boxdiv{
    height: 180px;
}

.myorderlist .box1{
    width: 30%;
    background-color: white;
    overflow: hidden;
    padding: 20px;
}
.myorderlist .box1 img{
    width: 80%
}
.myorderlist .box2{
    width: 80%;
    background-color: white;
    padding-top: 30px;
    font-weight: bold;
}
.myorderlist .box3{
    width: 30%;
    background-color: white;
    padding: 49px 20px 10px;
    text-align: center;
}
.myorderlist .box3 p{
    font-size: 10pxpx;
}

.myorderbox2{
    width: 40%;
    height: 200px;
    background-color: rgb(215, 250, 225);
    margin: 0px 10px;
}